// import pako from 'pako' // 
import { record } from '../utils/screen-record'

class GlobalRecorder {
  constructor() {
    this.recorder = null
    this.events = []
    this.isRecording = false
    this.listeners = []
    this.recordingId = null
    this.recordingStartTime = null
    this.maxRecordingDuration = 3 * 60 * 1000 // 3
    console.log('GlobalRecorder')
  }

  // 
  static getInstance() {
    if (!GlobalRecorder.instance) {
      GlobalRecorder.instance = new GlobalRecorder()
    }
    return GlobalRecorder.instance
  }

  // 
  addListener(callback) {
    this.listeners.push(callback)
  }

  // 
  removeListener(callback) {
    const index = this.listeners.indexOf(callback)
    if (index > -1) {
      this.listeners.splice(index, 1)
    }
  }

  // 
  emit(event, data) {
    this.listeners.forEach(callback => {
      try {
        callback(event, data)
      } catch (error) {
        console.error(':', error)
      }
    })
  }

  // 
  async startRecording(metadata = {}) {
    if (this.isRecording) {
      console.warn('')
      return false
    }

    try {
      console.log('GlobalRecorder...')
      
      this.recordingId = `rec_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
      this.recordingStartTime = Date.now()
      this.events = []
      
      // rrweb
      if (typeof window.rrweb === 'undefined') {
        throw new Error('rrweb')
      }
      
      // rrweb.recordthis.events
      this.recorder = window.rrweb.record({
        emit: (event) => {
          // 
          this.events.push(event)
          console.log('GlobalRecorder:', event.type, ':', this.events.length)
        },
        checkoutEveryNms: 30 * 1000, // 30
        recordCanvas: true,
        collectFonts: true,
        inlineStylesheet: true,
        ...metadata
      })
      
      if (this.recorder && typeof this.recorder === 'function') {
        this.isRecording = true
        this.emit('started', { recordingId: this.recordingId })
        console.log('GlobalRecorder')
        return true
      } else {
        throw new Error('')
      }
    } catch (error) {
      console.error('GlobalRecorder:', error)
      this.isRecording = false
      this.emit('error', error)
      return false
    }
  }

  // 
  startTimeoutTimer() {
    if (this.timeoutTimer) {
      clearTimeout(this.timeoutTimer)
    }
    
    this.timeoutTimer = setTimeout(() => {
      this.handleRecordingTimeout()
    }, this.maxRecordingDuration)
    
    console.log('', this.maxRecordingDuration / 1000, '')
  }

  // 
  async handleRecordingTimeout() {
    console.log('')
    
    if (this.isRecording) {
      try {
        await this.stopRecording()
        this.emit('timeout', {
          duration: this.maxRecordingDuration,
          message: '3'
        })
      } catch (error) {
        console.error(':', error)
      }
    }
  }

  // 
  clearTimeoutTimer() {
    if (this.timeoutTimer) {
      clearTimeout(this.timeoutTimer)
      this.timeoutTimer = null
      console.log('')
    }
  }

  // 
  async stopRecording() {
    if (!this.isRecording) {
      console.warn('')
      return null
    }

    try {
      console.log('GlobalRecorder...')
      
      if (this.recorder && typeof this.recorder === 'function') {
        this.recorder() // 
      }
      
      this.isRecording = false
      const events = [...this.events]
      
      console.log('GlobalRecorder, :', events.length)
      console.log('GlobalRecorder3:', events.slice(0, 3))
      
      this.emit('stopped', { events })
      
      return events
    } catch (error) {
      console.error('GlobalRecorder:', error)
      this.isRecording = false
      this.emit('error', error)
      return null
    }
  }

  // 
  getRecordingStatus() {
    return {
      isRecording: this.isRecording,
      recordingId: this.recordingId,
      eventCount: this.events.length,
      startTime: this.recordingStartTime
    }
  }

  // 
  async stopRecordingAndProcess(additionalInfo = {}) {
    try {
      console.log('GlobalRecorder...')
      
      const events = await this.stopRecording()
      
      if (!events || events.length === 0) {
        return {
          success: false,
          error: ''
        }
      }

      console.log('GlobalRecorder:', events.length)
      
      // 
      const validEvents = events.filter(event => event && event.type !== undefined && event.timestamp)
      if (validEvents.length === 0) {
        return {
          success: false,
          error: ''
        }
      }
      
      console.log('GlobalRecorder:', validEvents.length)
      
      // 
      const duration = this.recordingStartTime ? Date.now() - this.recordingStartTime : 0
      
      // 
      const baseData = {
        recordingId: this.recordingId,
        events: validEvents,
        startTime: new Date(this.recordingStartTime).toISOString(),
        endTime: new Date().toISOString(),
        duration: duration,
        totalEvents: validEvents.length,
        ...additionalInfo
      }
      
      // 
      const originalData = JSON.stringify(baseData)
      const originalSize = new Blob([originalData]).size
      
      console.log('GlobalRecorder:', originalSize, '')
      
      // pako
      if (typeof window.pako === 'undefined') {
        throw new Error('pako')
      }
      
      const compressed = window.pako.gzip(originalData)
      const compressedSize = compressed.length
      const compressionRatio = ((1 - compressedSize / originalSize) * 100).toFixed(2) + '%'
      
      console.log('GlobalRecorder:', compressedSize, ':', compressionRatio)
      
      // 
      const finalData = {
        ...baseData,
        compressedEvents: Array.from(compressed) // 
      }
      
      const result = {
        success: true,
        recordingResult: {
          events: validEvents,
          duration: duration,
          eventCount: validEvents.length
        },
        data: {
          recordingId: this.recordingId,
          startTime: baseData.startTime,
          endTime: baseData.endTime,
          duration: duration,
          finalData: finalData,
          sizes: {
            original: originalSize,
            compressed: compressedSize
          },
          compressionRatio: compressionRatio
        }
      }
      
      console.log('GlobalRecorder')
      return result
      
    } catch (error) {
      console.error('GlobalRecorder:', error)
      return {
        success: false,
        error: error.message
      }
    }
  }

  // 
  async finishRecording(businessData = {}) {
    console.log('GlobalRecorder:', businessData)
    
    try {
      // 
      const result = await this.stopRecordingAndProcess(businessData)
      
      // 
      this.emit('recording-finished', result)
      
      return result
    } catch (error) {
      console.error('GlobalRecorder:', error)
      const errorResult = {
        success: false,
        error: error.message,
        message: ''
      }
      
      this.emit('recording-finished', errorResult)
      return errorResult
    }
  }
}

export default GlobalRecorder 