<template>
  <RecordWrapper 
    :auto-start="false"
    :show-controls="false"
    :stop-on-destroy="false"
    :require-consent="false"
    :metadata="{ action: 'purchase-step-2', productId: productId }"
    @record-timeout="handleRecordTimeout"
    @timeout-confirmed="handleTimeoutConfirmed"
  >
    <div class="purchase-step">
      <van-nav-bar 
        title="" 
        left-arrow 
        fixed
        @click-left="$router.back()"
      />
      
      <div class="content" v-if="product">
        <!--  -->
        <div class="product-info-card">
          <div class="product-header">
            <h3 class="product-name">{{ product.name }}</h3>
            <div class="product-return">{{ product.expectedReturn }}</div>
          </div>
        </div>

        <!--  -->
        <div class="steps-container">
          <van-steps :active="1" direction="horizontal">
            <van-step></van-step>
            <van-step></van-step>
            <van-step></van-step>
            <van-step></van-step>
          </van-steps>
        </div>

        <!--  -->
        <div class="step-content">
          <div class="step-card">
            <h4 class="step-title"></h4>
            <van-form ref="purchaseForm" @submit="onFormSubmit">
              <van-field
                v-model="purchaseForm.amount"
                name="amount"
                label=""
                placeholder=""
                type="number"
                :rules="[{ required: true, message: '' }]"
              />
              <van-field
                v-model="purchaseForm.bankCard"
                name="bankCard"
                label=""
                placeholder=""
                :rules="[{ required: true, message: '' }]"
              />
              <van-field
                v-model="purchaseForm.password"
                name="password"
                label=""
                placeholder=""
                type="password"
                :rules="[{ required: true, message: '' }]"
              />
              <div class="step-actions">
                <van-button type="primary" size="large" block round @click="validateAndNext">
                  
                </van-button>
              </div>
            </van-form>
          </div>
        </div>
      </div>
    </div>
  </RecordWrapper>
</template>

<script>
import RecordWrapper from '@/components/RecordWrapper.vue'

export default {
  name: 'PurchaseStepTwo',
  components: {
    RecordWrapper
  },
  data() {
    return {
      productId: null,
      product: null,
      purchaseForm: {
        amount: '',
        bankCard: '',
        password: ''
      },
      products: {
        1: {
          id: 1,
          name: '',
          description: '',
          expectedReturn: '4.2%',
          minAmount: '1000',
          term: '90'
        },
        2: {
          id: 2,
          name: '',
          description: '',
          expectedReturn: '6.8%',
          minAmount: '5000',
          term: '180'
        },
        3: {
          id: 3,
          name: '',
          description: '',
          expectedReturn: '9.5%',
          minAmount: '10000',
          term: '365'
        }
      }
    }
  },
  created() {
    this.loadProduct()
    this.loadFormData()
  },
  methods: {
    loadProduct() {
      this.productId = parseInt(this.$route.query.id) || parseInt(this.$route.params.id)
      this.product = this.products[this.productId]
      
      if (!this.product) {
        this.$toast.fail('')
        this.$router.back()
        return
      }
    },

    loadFormData() {
      // sessionStorage
      const savedForm = sessionStorage.getItem('purchaseForm')
      if (savedForm) {
        try {
          this.purchaseForm = JSON.parse(savedForm)
        } catch (error) {
          console.error(':', error)
        }
      }
    },

    saveFormData() {
      // sessionStorage
      sessionStorage.setItem('purchaseForm', JSON.stringify(this.purchaseForm))
    },
    
    // 
    validateAndNext() {
      // 
      if (!this.purchaseForm.amount) {
        this.$toast.fail('')
        return
      }
      if (!this.purchaseForm.bankCard) {
        this.$toast.fail('')
        return
      }
      if (!this.purchaseForm.password) {
        this.$toast.fail('')
        return
      }
      
      // 
      const minAmount = parseInt(this.product.minAmount.replace(/[^\d]/g, ''))
      if (parseInt(this.purchaseForm.amount) < minAmount) {
        this.$toast.fail(`${this.product.minAmount}`)
        return
      }
      
      // 
      if (this.purchaseForm.bankCard.length < 16) {
        this.$toast.fail('')
        return
      }
      
      // 
      if (this.purchaseForm.password.length < 6) {
        this.$toast.fail('6')
        return
      }
      
      // 
      this.saveFormData()
      
      // 
      this.$router.push({
        name: 'PurchaseStepThree',
        query: { id: this.productId }
      })
    },

    // 
    onFormSubmit() {
      this.validateAndNext()
    },

    // 
    handleRecordTimeout() {
      console.log('2')
    },

    // 
    handleTimeoutConfirmed() {
      // 
      this.$router.push('/')
    }
  },
  watch: {
    // 
    purchaseForm: {
      handler() {
        this.saveFormData()
      },
      deep: true
    }
  }
}
</script>

