<template>
  <RecordWrapper 
    :auto-start="true"
    :show-controls="false"
    :stop-on-destroy="false"
    :require-consent="true"
    :metadata="{ action: 'purchase-step-1', productId: productId, flow: 'purchase-start' }"
    @record-timeout="handleRecordTimeout"
    @timeout-confirmed="handleTimeoutConfirmed"
    @record-started="onRecordStarted"
    @record-declined="onRecordDeclined"
    ref="recordWrapper"
  >
    <div class="purchase-step">
      <van-nav-bar 
        title="" 
        left-arrow 
        fixed
        @click-left="$router.back()"
      />
      
      <div class="content" v-if="product">
        <!--  -->
        <div class="product-info-card">
          <div class="product-header">
            <h3 class="product-name">{{ product.name }}</h3>
            <div class="product-return">{{ product.expectedReturn }}</div>
          </div>
          <p class="product-desc">{{ product.description }}</p>
        </div>

        <!--  -->
        <div class="steps-container">
          <van-steps :active="0" direction="horizontal">
            <van-step></van-step>
            <van-step></van-step>
            <van-step></van-step>
            <van-step></van-step>
          </van-steps>
        </div>

        <!--  -->
        <div class="step-content">
          <div class="step-card">
            <h4 class="step-title"></h4>
            <van-cell-group>
              <van-cell title="" :value="product.name" />
              <van-cell title="" :value="product.expectedReturn" />
              <van-cell title="" :value="product.minAmount" />
              <van-cell title="" :value="product.term" />
            </van-cell-group>
            <div class="step-actions">
              <van-button type="primary" size="large" block round @click="nextStep">
                
              </van-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </RecordWrapper>
</template>

<script>
import RecordWrapper from '@/components/RecordWrapper.vue'

export default {
  name: 'PurchaseStepOne',
  components: {
    RecordWrapper
  },
  data() {
    return {
      productId: null,
      product: null,
      products: {
        1: {
          id: 1,
          name: '',
          description: '',
          expectedReturn: '4.2%',
          minAmount: '1000',
          term: '90',
          image: 'https://img.yzcdn.cn/vant/cat.jpeg'
        },
        2: {
          id: 2,
          name: '',
          description: '',
          expectedReturn: '6.8%',
          minAmount: '5000',
          term: '180',
          image: 'https://img.yzcdn.cn/vant/cat.jpeg'
        },
        3: {
          id: 3,
          name: '',
          description: '',
          expectedReturn: '9.5%',
          minAmount: '10000',
          term: '365',
          image: 'https://img.yzcdn.cn/vant/cat.jpeg'
        }
      }
    }
  },
  created() {
    this.loadProduct()
  },
  methods: {
    loadProduct() {
      this.productId = parseInt(this.$route.query.id) || parseInt(this.$route.params.id)
      this.product = this.products[this.productId]
      
      if (!this.product) {
        this.$toast.fail('')
        this.$router.back()
        return
      }
    },
    
    nextStep() {
      // 
      this.$router.push({
        name: 'PurchaseStepTwo',
        query: { id: this.productId }
      })
    },

    // 
    handleRecordTimeout() {
      console.log('1')
    },

    // 
    handleTimeoutConfirmed() {
      // 
      this.$router.push('/')
    },

    onRecordStarted() {
      console.log('')
    },

    onRecordDeclined() {
      console.log('')
    }
  }
}
</script>

