<template>
  <RecordWrapper 
    :auto-start="false"
    :show-controls="false"
    :stop-on-destroy="false"
    :require-consent="false"
    :metadata="{ action: 'purchase-step-3', productId: productId }"
    @record-timeout="handleRecordTimeout"
    @timeout-confirmed="handleTimeoutConfirmed"
  >
    <div class="purchase-step">
      <van-nav-bar 
        title="" 
        left-arrow 
        fixed
        @click-left="$router.back()"
      />
      
      <div class="content" v-if="product">
        <!--  -->
        <div class="product-info-card">
          <div class="product-header">
            <h3 class="product-name">{{ product.name }}</h3>
            <div class="product-return">{{ product.expectedReturn }}</div>
          </div>
        </div>

        <!--  -->
        <div class="steps-container">
          <van-steps :active="2" direction="horizontal">
            <van-step></van-step>
            <van-step></van-step>
            <van-step></van-step>
            <van-step></van-step>
          </van-steps>
        </div>

        <!--  -->
        <div class="step-content">
          <div class="step-card">
            <h4 class="step-title"></h4>
            <van-cell-group>
              <van-cell title="" :value="product.name" />
              <van-cell title="" :value="`${purchaseForm.amount}`" />
              <van-cell title="" :value="expectedProfit" />
              <van-cell title="" :value="maskedBankCard" />
            </van-cell-group>
            
            <div class="risk-notice">
              <van-notice-bar
                left-icon="warning-o"
                text=""
              />
            </div>

            <div class="step-actions">
              <van-button type="primary" size="large" block round @click="confirmPurchase" :loading="purchasing">
                
              </van-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </RecordWrapper>
</template>

<script>
import RecordWrapper from '@/components/RecordWrapper.vue'

export default {
  name: 'PurchaseStepThree',
  components: {
    RecordWrapper
  },
  data() {
    return {
      productId: null,
      product: null,
      purchaseForm: {
        amount: '',
        bankCard: '',
        password: ''
      },
      purchasing: false,
      products: {
        1: {
          id: 1,
          name: '',
          description: '',
          expectedReturn: '4.2%',
          minAmount: '1000',
          term: '90'
        },
        2: {
          id: 2,
          name: '',
          description: '',
          expectedReturn: '6.8%',
          minAmount: '5000',
          term: '180'
        },
        3: {
          id: 3,
          name: '',
          description: '',
          expectedReturn: '9.5%',
          minAmount: '10000',
          term: '365'
        }
      }
    }
  },
  computed: {
    expectedProfit() {
      if (!this.purchaseForm.amount || !this.product) return '0'
      const rate = parseFloat(this.product.expectedReturn) / 100
      const profit = this.purchaseForm.amount * rate
      return `${profit.toFixed(2)}`
    },
    maskedBankCard() {
      if (!this.purchaseForm.bankCard) return ''
      const card = this.purchaseForm.bankCard
      return card.replace(/(\d{4})\d+(\d{4})/, '$1****$2')
    }
  },
  created() {
    this.loadProduct()
    this.loadFormData()
  },
  methods: {
    loadProduct() {
      this.productId = parseInt(this.$route.query.id) || parseInt(this.$route.params.id)
      this.product = this.products[this.productId]
      
      if (!this.product) {
        this.$toast.fail('')
        this.$router.push('/')
        return
      }
    },

    loadFormData() {
      // sessionStorage
      const savedForm = sessionStorage.getItem('purchaseForm')
      if (savedForm) {
        try {
          this.purchaseForm = JSON.parse(savedForm)
        } catch (error) {
          console.error(':', error)
          this.$toast.fail('')
          this.$router.push({
            name: 'PurchaseStepTwo',
            query: { id: this.productId }
          })
        }
      } else {
        this.$toast.fail('')
        this.$router.push({
          name: 'PurchaseStepTwo',
          query: { id: this.productId }
        })
      }
    },

    async confirmPurchase() {
      // 
      try {
        const GlobalRecorder = (await import('@/utils/global-recorder')).default
        const globalRecorder = GlobalRecorder.getInstance()
        const status = globalRecorder.getRecordingStatus()
        
        if (!status.isRecording) {
          this.$toast.fail('')
          this.$router.push('/')
          return
        }
      } catch (error) {
        console.error(':', error)
        this.$toast.fail('')
        return
      }
      
      this.purchasing = true
      
      try {
        // 
        await new Promise(resolve => setTimeout(resolve, 2000))
        
        // 
        const orderNo = 'PO' + Date.now()
        const purchaseTime = new Date().toLocaleString()
        
        // 
        const orderInfo = {
          orderNo,
          purchaseTime,
          productId: this.productId,
          productName: this.product.name,
          amount: this.purchaseForm.amount,
          expectedProfit: this.expectedProfit,
          bankCard: this.maskedBankCard
        }
        
        sessionStorage.setItem('orderInfo', JSON.stringify(orderInfo))
        
        this.purchasing = false
        
        // 
        this.$router.push({
          name: 'PurchaseStepFour',
          query: { id: this.productId, orderNo: orderNo }
        })
        
      } catch (error) {
        console.error(':', error)
        this.purchasing = false
        this.$toast.fail('')
      }
    },

    // 
    handleRecordTimeout() {
      console.log('3')
    },

    // 
    handleTimeoutConfirmed() {
      // 
      this.$router.push('/')
    }
  }
}
</script>

