<template>
  <div class="redeem-page">
    <div class="page-header">
      <h3></h3>
    </div>

    <div class="redeem-form">
      <van-cell-group title="">
        <van-cell title="" value="1" />
        <van-cell title="" value="50,000.00" />
        <van-cell title="" value="1,245.67" />
        <van-cell title="" value="45" />
      </van-cell-group>

      <van-cell-group title="">
        <van-field v-model="form.redeemAmount" label="" placeholder="" type="number" />
        <van-cell title="">
          <template #right-icon>
            <van-switch v-model="form.isFullRedeem" @change="onRedeemTypeChange" />
            <span style="margin-left: 10px;">{{ form.isFullRedeem ? '' : '' }}</span>
          </template>
        </van-cell>
        <van-field v-model="form.bankCard" label="" placeholder="" />
      </van-cell-group>

      <div class="redeem-notice">
        <h4></h4>
        <ul>
          <li></li>
          <li>T+1</li>
          <li>1</li>
        </ul>
        <div class="agree-section">
          <van-switch v-model="form.agreeRedeem" />
          <span></span>
        </div>
      </div>

      <div class="demo-actions">
        <van-button type="warning" block @click="calculateFee"></van-button>
        <van-button type="danger" block @click="confirmRedeem"></van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RedeemPage',
  data() {
    return {
      form: {
        redeemAmount: '',
        isFullRedeem: false,
        bankCard: '',
        agreeRedeem: false
      }
    }
  },
  methods: {
    onRedeemTypeChange(value) {
      if (value) {
        this.form.redeemAmount = '50000'
      } else {
        this.form.redeemAmount = ''
      }
    },
    calculateFee() {
      if (!this.form.redeemAmount) {
        this.$toast.fail('')
        return
      }
      const fee = (this.form.redeemAmount * 0.001).toFixed(2)
      this.$toast.success(`: ${fee}`)
    },
    confirmRedeem() {
      if (!this.form.agreeRedeem) {
        this.$toast.fail('')
        return
      }
      if (!this.form.redeemAmount || !this.form.bankCard) {
        this.$toast.fail('')
        return
      }
      this.$toast.success('')
    }
  }
}
</script>

