<template>
  <div class="record-wrapper">
    <!--  -->
    <van-dialog
      v-model="showRecordDialog"
      title=""
      :show-cancel-button="true"
      confirm-button-text=""
      cancel-button-text=""
      :before-close="handleDialogClose"
      :close-on-click-overlay="false"
    >
      <div class="record-dialog-content">
        <div class="dialog-icon">
          <van-icon name="video-o" size="48" color="#1989fa" />
        </div>
        <p class="dialog-text">
          
        </p>
        <p class="dialog-note">
           <br>
           <br>
           
        </p>
      </div>
    </van-dialog>

    <!--  -->
    <van-dialog
      v-model="showTimeoutDialog"
      title=""
      :show-cancel-button="false"
      confirm-button-text=""
      @confirm="handleTimeout"
    >
      <div class="timeout-dialog-content">
        <div class="dialog-icon">
          <van-icon name="warning-o" size="48" color="#ff6b35" />
        </div>
        <p class="dialog-text">
          3
        </p>
      </div>
    </van-dialog>

    <!--  -->
    <div v-if="showIndicator && isRecording" class="record-indicator">
      <div class="record-dot"></div>
      <span> {{ recordingTime }}</span>
    </div>

    <!--  -->
    <div v-if="showControls" class="record-controls">
      <van-button 
        v-if="!isRecording" 
        type="primary" 
        size="small"
        round
        @click="showRecordingConsent"
        :loading="loading"
        icon="video-o"
      >
        
      </van-button>
      <van-button 
        v-else 
        type="danger" 
        size="small"
        round
        @click="stopRecord"
        :loading="loading"
        icon="pause"
      >
        
      </van-button>
    </div>

    <!--  -->
    <div class="content-wrapper">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import GlobalRecorder from '@/utils/global-recorder'

export default {
  name: 'RecordWrapper',
  props: {
    // 
    showIndicator: {
      type: Boolean,
      default: false
    },
    // 
    showControls: {
      type: Boolean,
      default: true
    },
    // 
    recordOptions: {
      type: Object,
      default: () => ({})
    },
    // 
    autoStart: {
      type: Boolean,
      default: false
    },
    // 
    metadata: {
      type: Object,
      default: () => ({})
    },
    // 
    stopOnDestroy: {
      type: Boolean,
      default: false
    },
    // 3
    timeoutDuration: {
      type: Number,
      default: 3 * 60 * 1000 // 3
    },
    // 
    requireConsent: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      globalRecorder: null,
      isRecording: false,
      loading: false,
      showRecordDialog: false,
      showTimeoutDialog: false,
      recordingStartTime: null,
      recordingTimer: null,
      timeoutTimer: null,
      recordingTime: '00:00',
      hasUserConsent: false
    }
  },
  async mounted() {
    // 
    this.globalRecorder = GlobalRecorder.getInstance()
    
    // 
    this.globalRecorder.addListener(this.onRecordEvent)
    
    // 
    const status = this.globalRecorder.getRecordingStatus()
    this.isRecording = status.isRecording

    // 
    if (this.isRecording) {
      this.startRecordingTimer()
    }

    // 
    if (this.autoStart && !this.isRecording) {
      setTimeout(() => {
        if (this.requireConsent && !this.hasUserConsent) {
          // 
          console.log('RecordWrapper')
          this.showRecordingConsent()
        } else {
          // 
          console.log('RecordWrapper')
          this.startRecord()
        }
      }, 500) // 
    }
  },
  beforeDestroy() {
    // 
    this.clearTimers()
    
    // 
    if (this.globalRecorder) {
      this.globalRecorder.removeListener(this.onRecordEvent)
    }
    
    // stopOnDestroy
    if (this.isRecording && this.stopOnDestroy) {
      this.stopRecord()
    }
  },
  methods: {
    // 
    showRecordingConsent() {
      if (this.requireConsent && !this.isRecording) {
        this.showRecordDialog = true
      } else if (!this.requireConsent && !this.isRecording) {
        // 
        this.startRecord()
      }
    },

    // 
    handleDialogClose(action) {
      this.showRecordDialog = false
      if (action === 'confirm') {
        // 
        this.hasUserConsent = true
        this.startRecord()
      } else {
        // 
        this.$toast('')
        this.$emit('record-declined')
      }
    },

    // 
    async startRecord() {
      if (this.loading || this.isRecording) return

      this.loading = true
      try {
        const success = await this.globalRecorder.startRecording({
          ...this.metadata,
          component: this.$options.name,
          route: this.$route.path,
          startTime: new Date().toISOString()
        })

        if (success) {
          this.recordingStartTime = Date.now()
          this.startRecordingTimer()
          this.startTimeoutTimer()
          this.$emit('record-started')
          this.$toast.success('')
        } else {
          this.$toast.fail('')
        }
      } catch (error) {
        console.error(':', error)
        this.$toast.fail('')
      } finally {
        this.loading = false
      }
    },

    // 
    async stopRecord() {
      if (this.loading || !this.isRecording) return

      this.loading = true
      try {
        this.clearTimers()
        const result = await this.globalRecorder.stopRecording()
        this.$emit('record-stopped', result)
        
        if (result) {
          this.$toast.success('')
        } else {
          this.$toast.fail('')
        }
      } catch (error) {
        console.error(':', error)
        this.$toast.fail('')
      } finally {
        this.loading = false
      }
    },

    // 
    async stopRecordingAndProcess(additionalInfo = {}) {
      if (this.loading || !this.isRecording) {
        return {
          success: false,
          error: ''
        }
      }

      this.loading = true
      try {
        this.clearTimers()
        
        // stopRecordingAndProcess
        const result = await this.globalRecorder.stopRecordingAndProcess({
          ...additionalInfo,
          component: this.$options.name,
          route: this.$route.path,
          stopTime: new Date().toISOString()
        })
        
        console.log('RecordWrapper:', result)
        
        if (result.success) {
          // 
          const uploadResult = await this.uploadVideoData(result.data, additionalInfo)
          
          console.log('RecordWrapper:', uploadResult)
          
          const finalResult = {
            success: true,
            events: result.recordingResult.events,
            data: result.data,
            uploadResult: uploadResult,
            stats: {
              originalSize: result.data.sizes.original,
              compressedSize: result.data.sizes.compressed,
              compressionRatio: result.data.compressionRatio,
              eventCount: result.data.finalData.totalEvents
            }
          }
          
          // 
          this.$emit('record-processed', finalResult)
          this.$toast.success('')
          
          return finalResult
        } else {
          console.warn('RecordWrapper:', result.error)
          const errorResult = { 
            success: false, 
            error: result.error,
            message: result.error === '' ? 
              '' : 
              ''
          }
          
          this.$emit('record-process-failed', errorResult)
          this.$toast.fail(': ' + result.error)
          
          return errorResult
        }
      } catch (error) {
        console.error('RecordWrapper:', error)
        const errorResult = { success: false, error: error.message }
        
        this.$emit('record-process-failed', errorResult)
        this.$toast.fail(': ' + error.message)
        
        return errorResult
      } finally {
        this.loading = false
      }
    },

    // 
    async uploadVideoData(recordingData, additionalInfo = {}) {
      try {
        console.log('RecordWrapper...')
        
        // 
        const uploadData = {
          // 
          recordingInfo: {
            duration: recordingData.duration,
            eventCount: recordingData.finalData.totalEvents,
            startTime: recordingData.startTime,
            endTime: recordingData.endTime,
            compressionRatio: recordingData.compressionRatio
          },
          
          // 
          eventsData: recordingData.finalData.compressedEvents,
          
          // 
          sizes: recordingData.sizes,
          
          // 
          businessInfo: {
            ...additionalInfo,
            component: this.$options.name,
            route: this.$route.path,
            userAgent: navigator.userAgent,
            timestamp: Date.now()
          },
          
          // 
          metadata: {
            ...this.metadata,
            recordingId: recordingData.recordingId || `rec_${Date.now()}`,
            version: '1.0'
          }
        }
        
        console.log('RecordWrapper:', JSON.stringify(uploadData).length, '')
        
        // 
        const response = await fetch('/api/upload-video-recording', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'X-Recording-Type': 'video-session'
          },
          body: JSON.stringify(uploadData)
        })
        
        if (!response.ok) {
          throw new Error(`: ${response.status} ${response.statusText}`)
        }
        
        const result = await response.json()
        
        console.log('RecordWrapper:', result)
        
        return {
          success: true,
          uploadId: result.uploadId || result.id,
          videoUrl: result.videoUrl || result.url,
          size: JSON.stringify(uploadData).length,
          timestamp: Date.now(),
          response: result
        }
      } catch (error) {
        console.error('RecordWrapper:', error)
        
        // 
        try {
          const localStorageKey = `video_recording_${Date.now()}`
          const localData = {
            ...recordingData,
            additionalInfo,
            uploadFailedAt: new Date().toISOString(),
            error: error.message
          }
          
          localStorage.setItem(localStorageKey, JSON.stringify(localData))
          console.log('RecordWrapper:', localStorageKey)
          
          return {
            success: false,
            error: error.message,
            localStorageKey: localStorageKey,
            fallbackStorage: true,
            message: ''
          }
        } catch (localError) {
          console.error('RecordWrapper:', localError)
          
          return {
            success: false,
            error: error.message,
            localError: localError.message,
            message: ''
          }
        }
      }
    },

    // 
    startRecordingTimer() {
      if (this.recordingTimer) {
        clearInterval(this.recordingTimer)
      }
      
      this.recordingTimer = setInterval(() => {
        if (this.recordingStartTime) {
          const elapsed = Date.now() - this.recordingStartTime
          this.recordingTime = this.formatTime(elapsed)
        }
      }, 1000)
    },

    // 
    startTimeoutTimer() {
      if (this.timeoutTimer) {
        clearTimeout(this.timeoutTimer)
      }
      
      this.timeoutTimer = setTimeout(() => {
        this.handleRecordingTimeout()
      }, this.timeoutDuration)
    },

    // 
    async handleRecordingTimeout() {
      console.log('')
      this.showTimeoutDialog = true
      
      // 
      if (this.isRecording) {
        await this.stopRecord()
      }
      
      this.$emit('record-timeout')
    },

    // 
    handleTimeout() {
      this.showTimeoutDialog = false
      // 
      this.$emit('timeout-confirmed')
    },

    // 
    clearTimers() {
      if (this.recordingTimer) {
        clearInterval(this.recordingTimer)
        this.recordingTimer = null
      }
      if (this.timeoutTimer) {
        clearTimeout(this.timeoutTimer)
        this.timeoutTimer = null
      }
    },

    // 
    formatTime(milliseconds) {
      const totalSeconds = Math.floor(milliseconds / 1000)
      const minutes = Math.floor(totalSeconds / 60)
      const seconds = totalSeconds % 60
      return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
    },

    // 
    getRecordingStatus() {
      return this.globalRecorder ? this.globalRecorder.getRecordingStatus() : null
    },

    // 
    onRecordEvent(event, data) {
      switch (event) {
        case 'started':
          this.isRecording = true
          break
        case 'stopped':
          this.isRecording = false
          this.clearTimers()
          this.recordingTime = '00:00'
          break
        case 'error':
          this.isRecording = false
          this.clearTimers()
          console.error(':', data)
          break
      }
    },

    // 
    async finishRecording(businessData = {}) {
      console.log('RecordWrapper:', businessData)
      
      try {
        // 
        if (!this.isRecording) {
          console.warn('RecordWrapper')
          const errorResult = {
            success: false,
            error: '',
            message: ''
          }
          this.$emit('recording-finished', errorResult)
          return errorResult
        }
        
        // GlobalRecorderfinishRecording
        const result = await this.globalRecorder.finishRecording(businessData)
        
        console.log('RecordWrapper:', result)
        
        // 
        this.$emit('recording-finished', result)
        
        return result
      } catch (error) {
        console.error('RecordWrapper:', error)
        const errorResult = {
          success: false,
          error: error.message,
          message: ''
        }
        
        this.$emit('recording-finished', errorResult)
        return errorResult
      }
    }
  }
}
</script>

<style scoped>
