// import { record } from 'rrweb' // 
// import pako from 'pako' // 

// record
export function record(options = {}) {
  if (typeof window.rrweb === 'undefined') {
    throw new Error('rrweb')
  }
  
  const events = []
  
  // rrweb.record
  const stopRecording = window.rrweb.record({
    emit(event) {
      events.push(event)
    },
    checkoutEveryNms: 30 * 1000, // 30
    ...options
  })
  
  return {
    stop: stopRecording,
    addEvents: (targetArray) => {
      // events
      targetArray.push(...events)
    },
    getEvents: () => [...events]
  }
}

class ScreenRecorder {
  constructor(options = {}) {
    this.events = []
    this.isRecording = false
    this.stopRecording = null
    this.metadata = {}
    this.options = {
      uploadUrl: '/api/record/upload',
      autoUpload: true,
      compressData: true,
      maxFileSize: 10 * 1024 * 1024, // 10MB
      ...options
    }
    console.log('ScreenRecorder:', this.options)
  }

  async startRecording(metadata = {}) {
    if (this.isRecording) {
      console.log('')
      return true
    }

    try {
      console.log(':', metadata)
      
      this.metadata = metadata
      this.events = []
      
      if (typeof window.rrweb === 'undefined') {
        throw new Error('rrweb')
      }

      // rrweb.record
      this.stopRecording = window.rrweb.record({
        emit: (event) => {
          this.events.push(event)
        },
        checkoutEveryNms: 30 * 1000, // 30
        recordCanvas: true,
        collectFonts: true,
        inlineStylesheet: true
      })

      this.isRecording = true
      console.log('')
      return true
    } catch (error) {
      console.error(':', error)
      this.isRecording = false
      return false
    }
  }

  async stopRecording() {
    if (!this.isRecording) {
      console.log('')
      return null
    }

    try {
      console.log('...')
      
      if (this.stopRecording) {
        this.stopRecording()
        this.stopRecording = null
      }

      this.isRecording = false
      
      const result = {
        events: [...this.events],
        metadata: this.metadata,
        timestamp: Date.now(),
        duration: 0,
        eventsCount: this.events.length
      }

      console.log(':', this.events.length)
      
      // 
      if (this.options.autoUpload && this.events.length > 0) {
        console.log('...')
        const uploadResult = await this.uploadRecording(result)
        result.uploadResult = uploadResult
      }

      // 
      this.events = []
      this.metadata = {}

      return result
    } catch (error) {
      console.error(':', error)
      this.isRecording = false
      return null
    }
  }

  async uploadRecording(recordingData) {
    try {
      console.log(':', recordingData.events.length)
      
      let dataToUpload = recordingData

      // 
      if (this.options.compressData) {
        console.log('...')
        dataToUpload = await this.compressRecordingData(recordingData)
        console.log('')
      }

      // 
      const dataSize = this.getDataSize(dataToUpload)
      if (dataSize > this.options.maxFileSize) {
        console.warn(':', this.formatFileSize(dataSize))
        // 
      }

      console.log(':', this.formatFileSize(dataSize))
      
      // 
      const uploadResult = await this.mockUpload(dataToUpload)
      console.log(':', uploadResult)
      
      return uploadResult
    } catch (error) {
      console.error(':', error)
      return { success: false, error: error.message }
    }
  }

  async compressRecordingData(data) {
    try {
      if (typeof window.pako === 'undefined') {
        throw new Error('pako')
      }
      
      const jsonStr = JSON.stringify(data)
      const originalSize = new Blob([jsonStr]).size
      
      console.log(':', this.formatFileSize(originalSize))
      
      // pako
      const compressed = window.pako.gzip(jsonStr)
      const compressedSize = compressed.length
      
      console.log(':', this.formatFileSize(compressedSize))
      console.log(':', ((1 - compressedSize / originalSize) * 100).toFixed(2) + '%')
      
      return {
        ...data,
        compressed: true,
        originalSize: originalSize,
        compressedSize: compressedSize,
        compressedData: Array.from(compressed) // 
      }
    } catch (error) {
      console.error(':', error)
      return data // 
    }
  }

  async mockUpload(data) {
    // 
    await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 2000))
    
    return {
      success: true,
      uploadId: 'upload_' + Date.now(),
      timestamp: Date.now(),
      message: ''
    }
  }

  getDataSize(data) {
    const jsonStr = JSON.stringify(data)
    return new Blob([jsonStr]).size
  }

  formatFileSize(bytes) {
    if (bytes === 0) return '0 B'
    const k = 1024
    const sizes = ['B', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  addCustomEvent(event) {
    if (this.isRecording) {
      this.events.push({
        type: 5, // 
        timestamp: Date.now(),
        data: {
          tag: 'custom',
          payload: event
        }
      })
    }
  }

  getRecordingStatus() {
    return {
      isRecording: this.isRecording,
      eventsCount: this.events.length,
      metadata: this.metadata
    }
  }
}

export default ScreenRecorder 