<template>
  <div class="multi-page-record">
    <van-nav-bar title="" fixed />
    
    <div class="content">
      <!--  -->
      <div class="control-section">
        <div class="status-info">
          <div class="status-text">
            <span v-if="!isRecording && !recordData"></span>
            <span v-if="isRecording">... ({{ currentPage }})</span>
            <span v-if="!isRecording && recordData"></span>
          </div>
          <div v-if="isRecording" class="recording-indicator">
            <div class="red-dot"></div>
            <span>REC</span>
          </div>
        </div>

        <div class="control-buttons">
          <van-button 
            v-if="!isRecording && !recordData"
            type="primary" 
            size="large" 
            block
            round
            @click="startRecord"
            :loading="loading"
            icon="video-o"
          >
            
          </van-button>

          <van-button 
            v-if="isRecording"
            type="danger" 
            size="large" 
            block
            round
            @click="stopRecord"
            :loading="loading"
            icon="stop"
          >
            
          </van-button>

          <div v-if="!isRecording && recordData" class="action-buttons">
            <van-button 
              type="primary" 
              size="large"
              round
              @click="playRecord"
              icon="play"
            >
              
            </van-button>
            <van-button 
              type="info" 
              size="large"
              round
              @click="downloadCompressedFile"
              :loading="downloading"
              icon="download"
            >
              
            </van-button>
            <van-button 
              type="success" 
              size="large"
              round
              @click="uploadRecord"
              :loading="uploading"
              icon="cloud-upload"
            >
              
            </van-button>
            <van-button 
              size="large"
              round
              @click="resetRecord"
              icon="refresh"
            >
              
            </van-button>
          </div>
        </div>
      </div>

      <!--  -->
      <div v-if="isRecording" class="navigation-section">
        <van-cell-group title=" ()">
          <van-cell 
            title="" 
            is-link 
            @click="navigateTo('home')"
            :class="{ active: currentPage === '' }"
          />
          <van-cell 
            title="" 
            is-link 
            @click="navigateTo('products')"
            :class="{ active: currentPage === '' }"
          />
          <van-cell 
            title="" 
            is-link 
            @click="navigateTo('product-detail')"
            :class="{ active: currentPage === '' }"
          />
          <van-cell 
            title="" 
            is-link 
            @click="navigateTo('purchase')"
            :class="{ active: currentPage === '' }"
          />
          <van-cell 
            title="" 
            is-link 
            @click="navigateTo('redeem')"
            :class="{ active: currentPage === '' }"
          />
        </van-cell-group>
      </div>

      <!--  -->
      <div v-if="recordData" class="record-info">
        <van-cell-group title="">
          <van-cell title="" :value="recordDuration" />
          <van-cell title="" :value="recordData.events ? recordData.events.length : 0" />
          <van-cell title="" :value="visitedPages.length" />
          <van-cell title="" :value="recordSize" />
          <van-cell title="" :value="recordTime" />
        </van-cell-group>
        
        <van-cell-group title="">
          <van-cell 
            v-for="(page, index) in visitedPages" 
            :key="index"
            :title="`${index + 1}. ${page.name}`" 
            :value="page.duration"
          />
        </van-cell-group>
      </div>

      <!--  -->
      <div v-if="showPlayer" class="player-section">
        <div class="player-header">
          <h3></h3>
          <van-button size="small" @click="closePlayer" icon="cross"></van-button>
        </div>
        <div ref="playerContainer" class="player-container"></div>
        <div class="player-controls">
          <van-button size="small" @click="togglePlay" :icon="isPlaying ? 'pause' : 'play'">
            {{ isPlaying ? '' : '' }}
          </van-button>
          <van-button size="small" @click="resetPlayer" icon="replay"></van-button>
        </div>
      </div>

      <!--  -->
      <div class="current-page-content">
        <component :is="currentPageComponent" @page-change="onPageChange" />
      </div>
    </div>
  </div>
</template>

<script>
// import { record } from 'rrweb' // 
// import rrwebPlayer from 'rrweb-player' // 
// import pako from 'pako' // 

// 
import HomePage from './pages/HomePage.vue'
import ProductsPage from './pages/ProductsPage.vue'
import ProductDetailPage from './pages/ProductDetailPage.vue'
import PurchasePage from './pages/PurchasePage.vue'
import RedeemPage from './pages/RedeemPage.vue'

export default {
  name: 'MultiPageRecord',
  components: {
    HomePage,
    ProductsPage,
    ProductDetailPage,
    PurchasePage,
    RedeemPage
  },
  data() {
    return {
      isRecording: false,
      loading: false,
      uploading: false,
      downloading: false,
      recordData: null,
      recordTime: '',
      stopFn: null,
      events: [],
      
      // 
      currentPage: '',
      currentPageComponent: 'HomePage',
      visitedPages: [],
      pageStartTime: null,
      
      // 
      showPlayer: false,
      player: null,
      isPlaying: false
    }
  },
  computed: {
    recordDuration() {
      if (!this.recordData || !this.recordData.metadata) return '0'
      const duration = Math.round(this.recordData.metadata.duration / 1000)
      return `${duration}`
    },
    recordSize() {
      if (!this.recordData) return '0KB'
      const size = JSON.stringify(this.recordData).length
      return `${(size / 1024).toFixed(2)}KB`
    }
  },
  methods: {
    // 
    async startRecord() {
      this.loading = true
      try {
        await this.waitForPageLoad()
        
        this.events = []
        this.recordData = null
        this.visitedPages = []
        this.pageStartTime = Date.now()
        
        // 
        this.visitedPages.push({
          name: this.currentPage,
          startTime: this.pageStartTime,
          url: window.location.href
        })
        
        // rrweb
        if (typeof window.rrweb === 'undefined') {
          throw new Error('rrweb')
        }
        
        // rrweb
        this.stopFn = window.rrweb.record({
          emit: (event) => {
            // 
            event.pageInfo = {
              pageName: this.currentPage,
              url: window.location.href,
              timestamp: Date.now()
            }
            this.events.push(event)
          },
          recordCanvas: true,
          collectFonts: true,
          recordCrossOriginIframes: true
        })
        
        this.isRecording = true
        this.recordTime = new Date().toLocaleString()
        this.$toast.success('')
      } catch (error) {
        console.error(':', error)
        this.$toast.fail('')
      } finally {
        this.loading = false
      }
    },

    // 
    async stopRecord() {
      this.loading = true
      try {
        if (this.stopFn) {
          this.stopFn()
          this.stopFn = null
        }
        
        // 
        if (this.visitedPages.length > 0) {
          const lastPage = this.visitedPages[this.visitedPages.length - 1]
          if (!lastPage.endTime) {
            lastPage.endTime = Date.now()
            lastPage.duration = `${Math.round((lastPage.endTime - lastPage.startTime) / 1000)}`
          }
        }
        
        this.isRecording = false
        
        // 
        this.recordData = {
          events: this.events,
          metadata: {
            recordTime: Date.now(),
            eventsCount: this.events.length,
            duration: this.events.length > 0 ? 
              this.events[this.events.length - 1].timestamp - this.events[0].timestamp : 0,
            visitedPages: this.visitedPages,
            totalPages: this.visitedPages.length
          }
        }
        
        this.$toast.success('')
      } catch (error) {
        console.error(':', error)
        this.$toast.fail('')
      } finally {
        this.loading = false
      }
    },

    // 
    navigateTo(pageName) {
      if (!this.isRecording) return
      
      // 
      if (this.visitedPages.length > 0) {
        const currentPageRecord = this.visitedPages[this.visitedPages.length - 1]
        if (!currentPageRecord.endTime) {
          currentPageRecord.endTime = Date.now()
          currentPageRecord.duration = `${Math.round((currentPageRecord.endTime - currentPageRecord.startTime) / 1000)}`
        }
      }
      
      // 
      const pageMap = {
        'home': { name: '', component: 'HomePage' },
        'products': { name: '', component: 'ProductsPage' },
        'product-detail': { name: '', component: 'ProductDetailPage' },
        'purchase': { name: '', component: 'PurchasePage' },
        'redeem': { name: '', component: 'RedeemPage' }
      }
      
      const pageInfo = pageMap[pageName]
      if (pageInfo) {
        this.currentPage = pageInfo.name
        this.currentPageComponent = pageInfo.component
        
        // 
        const newPageStartTime = Date.now()
        this.visitedPages.push({
          name: pageInfo.name,
          startTime: newPageStartTime,
          url: window.location.href + '#' + pageName
        })
        
        this.$toast(`${pageInfo.name}`)
      }
    },

    // 
    onPageChange(pageName) {
      console.log(':', pageName)
    },

    // 
    async playRecord() {
      if (!this.recordData || !this.recordData.events) {
        this.$toast.fail('')
        return
      }
      
      if (this.recordData.events.length < 2) {
        this.$toast.fail('')
        return
      }
      
      this.showPlayer = true
      await this.$nextTick()
      
      try {
        if (this.$refs.playerContainer) {
          this.$refs.playerContainer.innerHTML = ''
        }
        
        // rrweb-player
        if (typeof window.rrwebPlayer === 'undefined') {
          throw new Error('rrweb-player')
        }
        
        this.player = new window.rrwebPlayer({
          target: this.$refs.playerContainer,
          props: {
            events: this.recordData.events,
            width: 375,
            height: 400,
            autoPlay: false,
            showController: true,
            skipInactive: true,
            speed: 1
          }
        })
        
        this.isPlaying = false
        this.$toast.success('')
        
        if (this.player && this.player.addEventListener) {
          this.player.addEventListener('finish', () => {
            this.isPlaying = false
          })
        }
        
      } catch (error) {
        console.error(':', error)
        this.$toast.fail(': ' + error.message)
        this.showPlayer = false
      }
    },

    // 
    async downloadCompressedFile() {
      if (!this.recordData) {
        this.$toast.fail('')
        return
      }
      
      this.downloading = true
      try {
        const result = await this.generateCompressedFile()
        
        if (!result.success) {
          this.$toast.fail(result.message)
          return
        }
        
        const timestamp = new Date().toISOString().replace(/[:.]/g, '-')
        const filename = `multi-page-record-${timestamp}.json`
        
        const content = JSON.stringify(result.data, null, 2)
        this.downloadFile(content, filename, 'application/json')
        
        const sizeInfo = (result.size / 1024).toFixed(2)
        const optimizedText = result.optimized ? ' ()' : ''
        this.$toast.success(` (${sizeInfo}KB)${optimizedText}`)
        
      } catch (error) {
        console.error(':', error)
        this.$toast.fail('')
      } finally {
        this.downloading = false
      }
    },

    // 
    async uploadRecord() {
      if (!this.recordData) {
        this.$toast.fail('')
        return
      }
      
      this.uploading = true
      try {
        const result = await this.generateCompressedFile()
        
        if (!result.success) {
          this.$toast.fail(result.message)
          return
        }
        
        console.log(':', result.data)
        console.log(':', (result.size / 1024 / 1024).toFixed(2) + 'MB')
        console.log(':', this.visitedPages)
        
        await new Promise(resolve => setTimeout(resolve, 2000))
        
        this.$toast.success(` (${(result.size / 1024 / 1024).toFixed(2)}MB)`)
      } catch (error) {
        console.error(':', error)
        this.$toast.fail('')
      } finally {
        this.uploading = false
      }
    },

    // 
    async generateCompressedFile() {
      try {
        const MAX_SIZE = 2 * 1024 * 1024
        
        let optimizedData = this.optimizeRecordData(this.recordData)
        const jsonString = JSON.stringify(optimizedData)
        // pako
        if (typeof window.pako === 'undefined') {
          throw new Error('pako')
        }
        
        const compressed = window.pako.gzip(jsonString)
        
        const compressedData = {
          compressed: true,
          data: Array.from(compressed),
          originalSize: jsonString.length,
          compressedSize: compressed.length,
          metadata: optimizedData.metadata,
          timestamp: new Date().toISOString()
        }
        
        const finalJson = JSON.stringify(compressedData)
        const finalSize = finalJson.length
        
        console.log(':')
        console.log(':', (jsonString.length / 1024).toFixed(2) + 'KB')
        console.log(':', (compressed.length / 1024).toFixed(2) + 'KB')
        console.log('JSON:', (finalSize / 1024).toFixed(2) + 'KB')
        
        if (finalSize > MAX_SIZE) {
          const furtherOptimized = this.furtherOptimizeData(optimizedData)
          const recompressed = window.pako.gzip(JSON.stringify(furtherOptimized))
          
          const newCompressedData = {
            compressed: true,
            data: Array.from(recompressed),
            originalSize: JSON.stringify(furtherOptimized).length,
            compressedSize: recompressed.length,
            metadata: furtherOptimized.metadata,
            timestamp: new Date().toISOString(),
            optimized: true
          }
          
          const newFinalJson = JSON.stringify(newCompressedData)
          const newFinalSize = newFinalJson.length
          
          if (newFinalSize > MAX_SIZE) {
            return {
              success: false,
              message: ` (${(newFinalSize / 1024 / 1024).toFixed(2)}MB)`
            }
          }
          
          return {
            success: true,
            data: newCompressedData,
            size: newFinalSize,
            optimized: true
          }
        }
        
        return {
          success: true,
          data: compressedData,
          size: finalSize,
          optimized: false
        }
        
      } catch (error) {
        return {
          success: false,
          message: ''
        }
      }
    },

    // 
    optimizeRecordData(data) {
      const optimized = {
        events: data.events.map(event => {
          const optimizedEvent = {
            type: event.type,
            timestamp: event.timestamp,
            pageInfo: event.pageInfo // 
          }
          
          if (event.data) {
            optimizedEvent.data = {}
            
            switch (event.type) {
              case 0:
              case 1:
                optimizedEvent.data = {
                  href: event.data.href,
                  width: event.data.width,
                  height: event.data.height
                }
                break
              case 2:
                optimizedEvent.data = {
                  node: event.data.node,
                  initialOffset: event.data.initialOffset
                }
                break
              case 3:
                optimizedEvent.data = {
                  source: event.data.source,
                  type: event.data.type,
                  id: event.data.id
                }
                if (event.data.positions) optimizedEvent.data.positions = event.data.positions
                if (event.data.texts) optimizedEvent.data.texts = event.data.texts
                if (event.data.attributes) optimizedEvent.data.attributes = event.data.attributes
                break
              default:
                optimizedEvent.data = event.data
            }
          }
          
          return optimizedEvent
        }),
        metadata: data.metadata
      }
      
      return optimized
    },

    // 
    furtherOptimizeData(data) {
      const keyEvents = []
      const SAMPLE_INTERVAL = 150
      
      let lastTimestamp = 0
      
      data.events.forEach(event => {
        if (event.type === 0 || event.type === 1 || event.type === 2) {
          keyEvents.push(event)
          lastTimestamp = event.timestamp
        } else {
          if (event.timestamp - lastTimestamp >= SAMPLE_INTERVAL) {
            keyEvents.push(event)
            lastTimestamp = event.timestamp
          }
        }
      })
      
      return {
        events: keyEvents,
        metadata: {
          ...data.metadata,
          originalEventsCount: data.events.length,
          sampledEventsCount: keyEvents.length,
          optimized: true
        }
      }
    },

    // 
    downloadFile(content, filename, mimeType) {
      const blob = new Blob([content], { type: mimeType })
      const url = URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = filename
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      URL.revokeObjectURL(url)
    },

    waitForPageLoad() {
      return new Promise((resolve) => {
        if (document.readyState === 'complete') {
          setTimeout(resolve, 500)
        } else {
          window.addEventListener('load', () => {
            setTimeout(resolve, 500)
          })
        }
      })
    },

    resetRecord() {
      this.recordData = null
      this.events = []
      this.visitedPages = []
      this.currentPage = ''
      this.currentPageComponent = 'HomePage'
      this.closePlayer()
      this.$toast.success('')
    },

    // 
    togglePlay() {
      if (!this.player) return
      
      try {
        if (this.isPlaying) {
          if (typeof this.player.pause === 'function') {
            this.player.pause()
          }
        } else {
          if (typeof this.player.play === 'function') {
            this.player.play()
          }
        }
        this.isPlaying = !this.isPlaying
      } catch (error) {
        console.warn(':', error)
        this.$toast.fail('')
      }
    },

    resetPlayer() {
      if (!this.player) return
      
      try {
        if (typeof this.player.goto === 'function') {
          this.player.goto(0)
        } else if (typeof this.player.setCurrentTime === 'function') {
          this.player.setCurrentTime(0)
        }
        this.isPlaying = false
      } catch (error) {
        console.warn(':', error)
        this.$toast.fail('')
      }
    },

    closePlayer() {
      if (this.player) {
        try {
          if (typeof this.player.destroy === 'function') {
            this.player.destroy()
          } else if (typeof this.player.$destroy === 'function') {
            this.player.$destroy()
          } else if (this.player.$el && this.player.$el.parentNode) {
            this.player.$el.parentNode.removeChild(this.player.$el)
          }
        } catch (error) {
          console.warn(':', error)
        }
        this.player = null
      }
      
      if (this.$refs.playerContainer) {
        this.$refs.playerContainer.innerHTML = ''
      }
      
      this.showPlayer = false
      this.isPlaying = false
    }
  },

  beforeDestroy() {
    if (this.isRecording && this.stopFn) {
      this.stopFn()
    }
    this.closePlayer()
  }
}
</script>

