<template>
  <RecordWrapper 
    :auto-start="false"
    :show-controls="false"
    :stop-on-destroy="true"
    :require-consent="false"
    :metadata="{ action: 'purchase-step-4', productId: productId }"
    @record-timeout="handleRecordTimeout"
    @timeout-confirmed="handleTimeoutConfirmed"
    @recording-finished="onRecordingFinished"
    ref="recordWrapper"
  >
    <div class="purchase-step">
      <van-nav-bar 
        title="" 
        :left-arrow="false"
        fixed
      />
      
      <div class="content">
        <!--  -->
        <div class="steps-container">
          <van-steps :active="3" direction="horizontal">
            <van-step></van-step>
            <van-step></van-step>
            <van-step></van-step>
            <van-step></van-step>
          </van-steps>
        </div>

        <!--  -->
        <div class="step-content">
          <div class="step-card success-card">
            <div class="success-content">
              <van-icon name="success" size="50" color="#07c160" />
              <h3></h3>
              <p></p>
              
              <van-cell-group v-if="orderInfo">
                <van-cell title="" :value="orderInfo.orderNo" />
                <van-cell title="" :value="orderInfo.purchaseTime" />
                <van-cell title="" :value="orderInfo.productName" />
                <van-cell title="" :value="`${orderInfo.amount}`" />
                <van-cell title="" :value="orderInfo.expectedProfit" />
                <van-cell title="" :value="orderInfo.bankCard" />
              </van-cell-group>

              <!--  -->
              <div class="recording-status">
                <h4></h4>
                <div v-if="recordingResult === null" class="status-loading">
                  <van-loading size="20" />
                  <span>...</span>
                </div>
                
                <!--  -->
                <div v-else-if="recordingResult && typeof recordingResult === 'object' && recordingResult.success" class="recording-success-section">
                  <van-notice-bar 
                    type="success" 
                    text=""
                  />
                  
                  <div class="success-info">
                    <div class="success-item">
                      <span class="label">:</span>
                      <span class="value">{{ recordingResult.recordingResult ? recordingResult.recordingResult.eventCount : (recordingResult.data ? recordingResult.data.finalData.totalEvents : 0) }}</span>
                    </div>
                    <div class="success-item">
                      <span class="label">:</span>
                      <span class="value">{{ formatFileSize(recordingResult.data ? recordingResult.data.sizes.original : 0) }}</span>
                    </div>
                    <div class="success-item">
                      <span class="label">:</span>
                      <span class="value">{{ formatFileSize(recordingResult.data ? recordingResult.data.sizes.compressed : 0) }}</span>
                    </div>
                    <div class="success-item">
                      <span class="label">:</span>
                      <span class="value">{{ recordingResult.data ? recordingResult.data.compressionRatio : '0%' }}</span>
                    </div>
                    <div class="success-item">
                      <span class="label">:</span>
                      <van-tag :type="recordingResult.uploadResult && recordingResult.uploadResult.success ? 'success' : 'danger'">
                        {{ recordingResult.uploadResult && recordingResult.uploadResult.success ? '' : '' }}
                      </van-tag>
                    </div>
                    <div v-if="recordingResult.uploadResult && recordingResult.uploadResult.success" class="success-item">
                      <span class="label">ID:</span>
                      <span class="value">{{ recordingResult.uploadResult.uploadId || '' }}</span>
                    </div>
                  </div>

                  <!--  -->
                  <div v-if="recordingResult.recordingResult && recordingResult.recordingResult.events" class="replay-section">
                    <h4></h4>
                    <div class="replay-controls">
                      <van-button 
                        type="primary" 
                        size="small" 
                        @click="startReplay"
                        :disabled="isReplaying"
                      >
                        {{ isReplaying ? '...' : '' }}
                      </van-button>
                      <van-button 
                        v-if="isReplaying" 
                        size="small" 
                        @click="stopReplay"
                      >
                        
                      </van-button>
                    </div>
                    <div 
                      id="step-replay-container" 
                      class="replay-container"
                    ></div>
                  </div>
                </div>

                <!--  -->
                <div v-else class="recording-error-section">
                  <van-notice-bar 
                    type="danger" 
                    :text="recordingResult ? (recordingResult.message || recordingResult.error || '') : ''"
                  />
                </div>
              </div>

              <!--  -->
              <div class="step-actions">
                <van-button 
                  v-if="recordingResult && typeof recordingResult === 'object' && recordingResult.success" 
                  type="primary" 
                  size="large" 
                  block 
                  round 
                  @click="goHome"
                >
                  
                </van-button>
                <template v-else>
                  <van-button 
                    type="primary" 
                    size="large" 
                    block 
                    round 
                    @click="goToProducts"
                    style="margin-bottom: 12px;"
                  >
                    
                  </van-button>
                  <van-button 
                    type="default" 
                    size="large" 
                    block 
                    round 
                    @click="goHome"
                  >
                    
                  </van-button>
                </template>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </RecordWrapper>
</template>

<script>
import RecordWrapper from '@/components/RecordWrapper.vue'
import { Loading } from 'vant'

export default {
  name: 'PurchaseStepFour',
  components: {
    RecordWrapper,
    [Loading.name]: Loading
  },
  data() {
    return {
      productId: null,
      orderInfo: null,
      recordingResult: null,
      isReplaying: false,
      replayPlayer: null
    }
  },
  watch: {
    recordingResult: {
      handler(newVal, oldVal) {
        console.log('recordingResult')
        console.log(':', oldVal)
        console.log(':', newVal)
        console.log(':', typeof newVal)
        if (newVal) {
          console.log(':', Object.keys(newVal))
        }
      },
      immediate: true,
      deep: true
    }
  },
  async mounted() {
    this.loadOrderInfo()
    
    // RecordWrapper
    setTimeout(() => {
      this.finishRecording()
    }, 1500)
  },
  beforeDestroy() {
    // 
    this.stopReplay()
  },
  methods: {
    loadOrderInfo() {
      this.productId = parseInt(this.$route.query.id) || parseInt(this.$route.params.id)
      
      // sessionStorage
      const savedOrder = sessionStorage.getItem('orderInfo')
      if (savedOrder) {
        try {
          this.orderInfo = JSON.parse(savedOrder)
        } catch (error) {
          console.error(':', error)
        }
      }
      
      if (!this.orderInfo) {
        this.$toast.fail('')
        this.$router.push('/')
      }
    },

    //  - RecordWrapper
    async finishRecording() {
      try {
        console.log('...')
        
        // refRecordWrapperfinishRecording
        if (this.$refs.recordWrapper && typeof this.$refs.recordWrapper.finishRecording === 'function') {
          console.log('RecordWrapper.finishRecording')
          
          const result = await this.$refs.recordWrapper.finishRecording({
            purchaseInfo: {
              ...this.orderInfo,
              timestamp: Date.now(),
              page: 'PurchaseStepFour',
              action: 'purchase-completed'
            }
          })
          
          console.log('RecordWrapper.finishRecording:', result)
          console.log(':', typeof result)
          
          // onRecordingFinished
          // 
          if (result && typeof result === 'object') {
            this.recordingResult = result
          }
        } else {
          console.warn('RecordWrapperfinishRecording')
          this.recordingResult = {
            success: false,
            error: '',
            message: ''
          }
        }
      } catch (error) {
        console.error(':', error)
        this.recordingResult = { 
          success: false, 
          error: error.message,
          message: ': ' + error.message
        }
      }
    },

    // 
    formatFileSize(bytes) {
      if (bytes === 0) return '0 B'
      const k = 1024
      const sizes = ['B', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    },

    goToProducts() {
      // 
      sessionStorage.removeItem('purchaseForm')
      sessionStorage.removeItem('orderInfo')
      this.$router.push('/')
    },

    goHome() {
      // 
      sessionStorage.removeItem('purchaseForm')
      sessionStorage.removeItem('orderInfo')
      this.$router.push('/')
    },

    // 
    handleRecordTimeout() {
      console.log('4')
    },

    // 
    handleTimeoutConfirmed() {
      // 
      this.$router.push('/')
    },

    //  - RecordWrapper
    onRecordingFinished(result) {
      console.log(':', result)
      console.log('result:', typeof result)
      console.log('result.success:', result ? result.success : 'result')
      
      // result
      if (result && typeof result === 'object') {
        this.recordingResult = result
        
        if (result.success) {
          this.$toast.success('')
        } else {
          this.$toast.fail(': ' + (result.message || result.error))
        }
      } else {
        console.error(':', result)
        this.recordingResult = {
          success: false,
          error: '',
          message: ''
        }
        this.$toast.fail('')
      }
    },

    // 
    async startReplay() {
      if (!this.recordingResult || !this.recordingResult.recordingResult || !this.recordingResult.recordingResult.events) {
        this.$toast.fail('')
        return
      }
      
      try {
        this.isReplaying = true
        
        // 
        const container = document.getElementById('step-replay-container')
        if (!container) {
          throw new Error('')
        }
        container.innerHTML = ''
        container.style.width = '100%'
        container.style.height = '600px'
        container.style.position = 'relative'
        container.style.overflow = 'visible'
        container.style.border = '1px solid #eee'
        container.style.borderRadius = '4px'
        container.style.backgroundColor = '#f5f5f5'
        
        console.log('PurchaseStepFour:', this.recordingResult.recordingResult.events.length)
        
        // 
        const events = this.recordingResult.recordingResult.events
        if (!Array.isArray(events) || events.length < 2) {
          throw new Error('')
        }
        
        // 
        const hasMetaEvent = events.some(e => e.type === 4)
        const hasSnapshot = events.some(e => e.type === 2)
        
        if (!hasMetaEvent || !hasSnapshot) {
          throw new Error('')
        }
        
        // rrweb-player
        if (typeof window.rrwebPlayer === 'undefined') {
          throw new Error('rrweb-player')
        }
        
        const rrwebPlayer = window.rrwebPlayer
        
        console.log('PurchaseStepFourrrweb-player')
        
        // container
        this.replayPlayer = new rrwebPlayer({
          target: container,
          props: {
            events: events,
            width: container.offsetWidth || 375,
            height: 550,
            autoPlay: false,
            showController: true,
            showWarning: false,
            showDebug: false,
            speed: 1,
            speedOption: [0.5, 1, 1.5, 2, 4],
            skipInactive: false,
            mouseTail: false,
            tags: {
              'virtual-styles': {}
            }
          }
        })
        
        console.log('PurchaseStepFourrrweb-player')
        
        // iframe
        setTimeout(() => {
          const iframes = container.querySelectorAll('iframe')
          iframes.forEach(iframe => {
            if (iframe.hasAttribute('sandbox')) {
              iframe.setAttribute('sandbox', 'allow-scripts allow-same-origin allow-forms allow-popups allow-modals')
            }
            iframe.style.width = '100%'
            iframe.style.height = '100%'
            iframe.style.border = 'none'
          })
          
          console.log('PurchaseStepFour')
        }, 500)
        
        // 
        if (this.replayPlayer && this.replayPlayer.$on) {
          this.replayPlayer.$on('finish', () => {
            this.isReplaying = false
            console.log('PurchaseStepFour')
          })
          
          this.replayPlayer.$on('start', () => {
            console.log('PurchaseStepFour')
          })
        }
        
        this.$toast.success('')
        
      } catch (error) {
        console.error('PurchaseStepFour:', error)
        this.isReplaying = false
        this.$toast.fail(': ' + error.message)
        
        // 
        const container = document.getElementById('step-replay-container')
        if (container) {
          container.innerHTML = `
            <div style="text-align: center; padding: 50px; color: #f56c6c; background: white;">
              <h4></h4>
              <p style="font-size: 12px; margin-top: 10px;">${error.message}</p>
              <p style="font-size: 10px; color: #999; margin-top: 10px;">
                : ${this.recordingResult.recordingResult && this.recordingResult.recordingResult.events ? this.recordingResult.recordingResult.events.length : 0}
              </p>
            </div>
          `
        }
      }
    },

    // 
    stopReplay() {
      if (this.replayPlayer) {
        try {
          if (this.replayPlayer.$destroy) {
            this.replayPlayer.$destroy()
          }
          this.replayPlayer = null
        } catch (error) {
          console.error(':', error)
        }
      }
      this.isReplaying = false
      
      // 
      const container = document.getElementById('step-replay-container')
      if (container) {
        container.innerHTML = '<div style="text-align: center; padding: 50px; color: #999;"></div>'
      }
    }
  }
}
</script>

