<template>
  <div class="record-player">
    <van-nav-bar 
      title="" 
      left-arrow 
      @click-left="$router.back()"
    />
    
    <div class="content">
      <!--  -->
      <van-cell-group title="">
        <van-cell
          v-for="record in records"
          :key="record.id"
          :title="record.title"
          :label="record.description"
          :value="record.duration"
          is-link
          @click="playRecord(record)"
        >
          <template #icon>
            <van-icon name="video-o" />
          </template>
        </van-cell>
      </van-cell-group>

      <!--  -->
      <div v-if="currentRecord" class="player-container">
        <h3>{{ currentRecord.title }}</h3>
        <div ref="playerContainer" class="player"></div>
        
        <div class="player-controls">
          <van-button 
            size="small" 
            @click="togglePlay"
            :icon="isPlaying ? 'pause' : 'play'"
          >
            {{ isPlaying ? '' : '' }}
          </van-button>
          <van-button 
            size="small" 
            @click="resetPlayer"
            icon="replay"
          >
            
          </van-button>
          <van-button 
            size="small" 
            @click="closePlayer"
            icon="cross"
          >
            
          </van-button>
        </div>
      </div>

      <!--  -->
      <div v-if="records.length === 0" class="empty-state">
        <van-icon name="video-o" size="50" color="#dcdee0" />
        <p></p>
        <van-button type="primary" size="small" @click="$router.push('/products')">
          
        </van-button>
      </div>
    </div>
  </div>
</template>

<script>
// import rrwebPlayer from 'rrweb-player' // 
import ScreenRecorder from '@/utils/screen-record'

export default {
  name: 'RecordPlayer',
  data() {
    return {
      records: [
        {
          id: 1,
          title: ' - ',
          description: '2024-01-15 14:30:25 | ',
          duration: '230',
          action: 'purchase',
          productId: 1,
          events: [] // 
        },
        {
          id: 2,
          title: ' - ',
          description: '2024-01-15 16:45:12 | ',
          duration: '145',
          action: 'redeem',
          productId: 2,
          events: [] // 
        }
      ],
      currentRecord: null,
      player: null,
      isPlaying: false
    }
  },
  methods: {
    async playRecord(record) {
      this.currentRecord = record
      
      // DOM
      await this.$nextTick()
      
      try {
        // 
        let events = record.events
        if (record.compressed) {
          events = ScreenRecorder.decompressData(record)
        }

        // 
        if (!events || events.length === 0) {
          events = this.generateDemoEvents()
        }

        // 2
        if (events.length < 2) {
          events = this.generateDemoEvents()
        }

        // 
        // rrweb-player
        if (typeof window.rrwebPlayer === 'undefined') {
          throw new Error('rrweb-player')
        }
        
        this.player = new window.rrwebPlayer({
          target: this.$refs.playerContainer,
          props: {
            events: events,
            width: 375,
            height: 667,
            autoPlay: false,
            showController: true,
            tags: {
              'virtual-styles': {
                'background-color': '#f7f8fa'
              }
            }
          }
        })

        // iframe sandbox
        setTimeout(() => {
          const iframes = this.$refs.playerContainer.querySelectorAll('iframe')
          iframes.forEach(iframe => {
            if (iframe.hasAttribute('sandbox')) {
              console.log('RecordPlayer iframe sandbox')
              iframe.setAttribute('sandbox', 'allow-scripts allow-same-origin allow-forms allow-popups allow-modals')
            }
          })
        }, 100)

        this.isPlaying = false
        this.$toast.success('')
      } catch (error) {
        console.error(':', error)
        this.$toast.fail(': ' + error.message)
      }
    },

    togglePlay() {
      if (!this.player) return
      
      try {
        if (this.isPlaying) {
          this.player.pause()
        } else {
          this.player.play()
        }
        this.isPlaying = !this.isPlaying
      } catch (error) {
        console.error(':', error)
        this.$toast.fail('')
      }
    },

    resetPlayer() {
      if (!this.player) return
      
      try {
        this.player.goto(0)
        this.isPlaying = false
      } catch (error) {
        console.error(':', error)
      }
    },

    closePlayer() {
      if (this.player) {
        try {
          this.player.destroy()
        } catch (error) {
          console.error(':', error)
        }
        this.player = null
      }
      this.currentRecord = null
      this.isPlaying = false
    },

    // 
    generateDemoEvents() {
      const now = Date.now()
      return [
        // 
        {
          type: 4,
          data: {
            href: window.location.origin,
            width: 375,
            height: 667
          },
          timestamp: now - 5000
        },
        // DOM
        {
          type: 2,
          data: {
            node: {
              type: 0,
              childNodes: [
                {
                  type: 1,
                  name: 'html',
                  publicId: '',
                  systemId: '',
                  id: 2
                },
                {
                  type: 2,
                  tagName: 'html',
                  attributes: {
                    lang: 'zh-CN'
                  },
                  childNodes: [
                    {
                      type: 2,
                      tagName: 'head',
                      attributes: {},
                      childNodes: [
                        {
                          type: 2,
                          tagName: 'title',
                          attributes: {},
                          childNodes: [
                            {
                              type: 3,
                              textContent: '',
                              id: 5
                            }
                          ],
                          id: 4
                        }
                      ],
                      id: 3
                    },
                    {
                      type: 2,
                      tagName: 'body',
                      attributes: {
                        style: 'margin: 0; padding: 20px; font-family: Arial, sans-serif; background-color: #f7f8fa;'
                      },
                      childNodes: [
                        {
                          type: 2,
                          tagName: 'div',
                          attributes: {
                            style: 'text-align: center; padding: 40px 20px; background: white; border-radius: 8px; margin: 20px 0;'
                          },
                          childNodes: [
                            {
                              type: 2,
                              tagName: 'h2',
                              attributes: {
                                style: 'color: #323233; margin-bottom: 20px;'
                              },
                              childNodes: [
                                {
                                  type: 3,
                                  textContent: '',
                                  id: 8
                                }
                              ],
                              id: 7
                            },
                            {
                              type: 2,
                              tagName: 'p',
                              attributes: {
                                style: 'color: #969799; margin-bottom: 30px;'
                              },
                              childNodes: [
                                {
                                  type: 3,
                                  textContent: '',
                                  id: 10
                                }
                              ],
                              id: 9
                            },
                            {
                              type: 2,
                              tagName: 'button',
                              attributes: {
                                id: 'demo-btn',
                                style: 'background: #1989fa; color: white; border: none; padding: 12px 24px; border-radius: 4px; cursor: pointer;'
                              },
                              childNodes: [
                                {
                                  type: 3,
                                  textContent: '',
                                  id: 12
                                }
                              ],
                              id: 11
                            }
                          ],
                          id: 6
                        }
                      ],
                      id: 13
                    }
                  ],
                  id: 14
                }
              ],
              id: 1
            }
          },
          timestamp: now - 4000
        },
        // 
        {
          type: 3,
          data: {
            source: 2,
            type: 1,
            x: 187,
            y: 400
          },
          timestamp: now - 3000
        },
        // 
        {
          type: 3,
          data: {
            source: 2,
            type: 2,
            x: 187,
            y: 400,
            id: 11
          },
          timestamp: now - 2000
        },
        // DOM
        {
          type: 3,
          data: {
            source: 0,
            texts: [
              {
                id: 12,
                value: ''
              }
            ]
          },
          timestamp: now - 1000
        }
      ]
    }
  },

  beforeDestroy() {
    this.closePlayer()
  }
}
</script>

<style scoped>
.record-player {
  min-height: 100vh;
  background-color: #f7f8fa;
}

.content {
  padding: 16px;
}

.player-container {
  margin-top: 20px;
  background: white;
  border-radius: 8px;
  padding: 20px;
}

.player-container h3 {
  margin-bottom: 15px;
  color: #323233;
  font-size: 16px;
}

.player {
  border: 1px solid #ebedf0;
  border-radius: 4px;
  margin-bottom: 15px;
  min-height: 300px;
  overflow: hidden;
}

.player-controls {
  display: flex;
  gap: 10px;
  justify-content: center;
}

.empty-state {
  text-align: center;
  padding: 60px 20px;
  background: white;
