<template>
  <div class="products-page">
    <div class="page-header">
      <h3></h3>
      <p></p>
    </div>

    <div class="filter-section">
      <van-field v-model="searchText" placeholder="" />
      <div class="filter-buttons">
        <van-button 
          v-for="type in productTypes" 
          :key="type"
          size="small"
          :type="selectedType === type ? 'primary' : 'default'"
          @click="filterByType(type)"
        >
          {{ type }}
        </van-button>
      </div>
    </div>

    <div class="products-list">
      <div 
        v-for="product in filteredProducts" 
        :key="product.id"
        class="product-card"
        @click="selectProduct(product)"
      >
        <div class="product-header">
          <h4>{{ product.name }}</h4>
          <span class="product-rate">{{ product.rate }}%</span>
        </div>
        <div class="product-info">
          <div class="info-item">
            <span class="label">:</span>
            <span class="value">{{ product.minAmount }}</span>
          </div>
          <div class="info-item">
            <span class="label">:</span>
            <span class="value">{{ product.period }}</span>
          </div>
          <div class="info-item">
            <span class="label">:</span>
            <span class="value">{{ product.riskLevel }}</span>
          </div>
        </div>
        <div class="product-actions">
          <van-button size="small" type="info" @click.stop="viewDetail(product)"></van-button>
          <van-button size="small" type="primary" @click.stop="buyProduct(product)"></van-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductsPage',
  data() {
    return {
      searchText: '',
      selectedType: '',
      productTypes: ['', '', '', ''],
      products: [
        {
          id: 1,
          name: '1',
          rate: 4.2,
          minAmount: 1,
          period: '90',
          riskLevel: 'R2',
          type: ''
        },
        {
          id: 2,
          name: '2',
          rate: 5.8,
          minAmount: 5,
          period: '180',
          riskLevel: 'R3',
          type: ''
        },
        {
          id: 3,
          name: '3',
          rate: 7.5,
          minAmount: 10,
          period: '365',
          riskLevel: 'R4',
          type: ''
        },
        {
          id: 4,
          name: '4',
          rate: 3.8,
          minAmount: 1,
          period: '60',
          riskLevel: 'R1',
          type: ''
        }
      ]
    }
  },
  computed: {
    filteredProducts() {
      let filtered = this.products
      
      // 
      if (this.selectedType !== '') {
        filtered = filtered.filter(p => p.type === this.selectedType)
      }
      
      // 
      if (this.searchText) {
        filtered = filtered.filter(p => 
          p.name.toLowerCase().includes(this.searchText.toLowerCase())
        )
      }
      
      return filtered
    }
  },
  methods: {
    filterByType(type) {
      this.selectedType = type
      this.$toast(`: ${type}`)
    },
    selectProduct(product) {
      this.$toast(` ${product.name}`)
    },
    viewDetail(product) {
      this.$toast(` ${product.name} `)
    },
    buyProduct(product) {
      this.$toast(` ${product.name}`)
    }
  }
}
</script>

