<template>
  <div class="product-detail">
    <van-nav-bar 
      title="" 
      left-arrow 
      @click-left="$router.back()"
    />
    
    <div class="content" v-if="product">
      <van-card
        :title="product.name"
        :desc="product.description"
        :price="product.expectedReturn"
        :thumb="product.image"
      >
        <template #tags>
          <van-tag 
            :type="product.riskLevel === '' ? 'success' : product.riskLevel === '' ? 'warning' : 'danger'"
          >
            {{ product.riskLevel }}
          </van-tag>
        </template>
      </van-card>

      <van-cell-group title="">
        <van-cell title="" :value="product.expectedReturn" />
        <van-cell title="" :value="product.minAmount" />
        <van-cell title="" :value="product.term" />
        <van-cell title="" :value="product.riskLevel" />
        <van-cell title="" :value="product.investmentDirection" />
      </van-cell-group>

      <van-cell-group title="">
        <van-cell 
          v-for="(feature, index) in product.features"
          :key="index"
          :title="feature"
        />
      </van-cell-group>

      <div class="actions">
        <van-button 
          type="primary" 
          size="large" 
          block
          @click="goToPurchase"
        >
          
        </van-button>
        <van-button 
          size="large" 
          block
          style="margin-top: 12px;"
          @click="goToRedeem"
        >
          
        </van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductDetail',
  data() {
    return {
      product: null,
      products: {
        1: {
          id: 1,
          name: '',
          description: '',
          expectedReturn: '4.2%',
          minAmount: '1000',
          term: '90',
          riskLevel: '',
          investmentDirection: '',
          image: 'https://img.yzcdn.cn/vant/cat.jpeg',
          features: [
            '',
            '',
            '',
            ''
          ]
        },
        2: {
          id: 2,
          name: '',
          description: '',
          expectedReturn: '6.8%',
          minAmount: '5000',
          term: '180',
          riskLevel: '',
          investmentDirection: '',
          image: 'https://img.yzcdn.cn/vant/cat.jpeg',
          features: [
            '',
            '',
            '',
            ''
          ]
        },
        3: {
          id: 3,
          name: '',
          description: '',
          expectedReturn: '9.5%',
          minAmount: '10000',
          term: '365',
          riskLevel: '',
          investmentDirection: '',
          image: 'https://img.yzcdn.cn/vant/cat.jpeg',
          features: [
            '',
            '',
            '',
            ''
          ]
        }
      }
    }
  },
  created() {
    this.loadProduct()
  },
  methods: {
    loadProduct() {
      const id = parseInt(this.$route.params.id)
      this.product = this.products[id]
    },
    goToPurchase() {
      this.$router.push(`/purchase/${this.product.id}`)
    },
    goToRedeem() {
      this.$router.push(`/redeem/${this.product.id}`)
    }
  },
  watch: {
    '$route'() {
      this.loadProduct()
    }
  }
}
</script>

<style scoped>
.product-detail {
  min-height: 100vh;
  background-color: #f7f8fa;
}

.content {
  padding: 16px;
}

.actions {
  margin-top: 20px;
  padding: 0 16px 20px;
}
</style> 