<template>
  <div class="purchase-page">
    <div class="page-header">
      <h3></h3>
    </div>

    <div class="purchase-form">
      <van-cell-group title="">
        <van-cell title="" value="1" />
        <van-cell title="" value="4.2%" />
      </van-cell-group>

      <van-cell-group title="">
        <van-field v-model="form.amount" label="" placeholder="" type="number" />
        <van-field v-model="form.name" label="" placeholder="" />
        <van-field v-model="form.phone" label="" placeholder="" />
        <van-field v-model="form.idCard" label="" placeholder="" />
      </van-cell-group>

      <div class="risk-notice">
        <h4></h4>
        <p></p>
        <div class="agree-section">
          <van-switch v-model="form.agreeRisk" />
          <span></span>
        </div>
      </div>

      <div class="demo-actions">
        <van-button type="warning" block @click="calculateIncome"></van-button>
        <van-button type="primary" block @click="confirmPurchase"></van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PurchasePage',
  data() {
    return {
      form: {
        amount: '',
        name: '',
        phone: '',
        idCard: '',
        agreeRisk: false
      }
    }
  },
  methods: {
    calculateIncome() {
      if (!this.form.amount) {
        this.$toast.fail('')
        return
      }
      const income = (this.form.amount * 10000 * 0.042 * 90 / 365).toFixed(2)
      this.$toast.success(`: ${income}`)
    },
    confirmPurchase() {
      if (!this.form.agreeRisk) {
        this.$toast.fail('')
        return
      }
      if (!this.form.amount || !this.form.name) {
        this.$toast.fail('')
        return
      }
      this.$toast.success('')
    }
  }
}
</script>

